<template>
  <v-container fill-height>
    <v-row>
      <v-col cols="12">
        <v-row align="center" justify="center">
          <div class="mb-16 text-h3 font-weight-light black--text">Register</div>
        </v-row>
        <v-row align="center" justify="center">
          <v-card class="pa-8" width="500">
            <v-form ref="loginForm" v-model="isFormValid">
              <v-text-field
                label="Nama"
                type="text"
                v-model="user.name"
                :rules="this.nameRules"
                required
              ></v-text-field>
              <v-text-field
                label="Username"
                type="text"
                v-model="user.username"
                :rules="this.usernameRules"
                required
              ></v-text-field>
              <v-text-field
                label="NISN"
                type="text"
                v-model="user.nisn"
                :rules="this.nisnRules"
                required
              ></v-text-field>
              <v-text-field
                label="No Telp (WA)"
                type="tel"
                v-model="user.phone"
                :rules="this.phoneNumRules"
                required
              ></v-text-field>
              <v-text-field
                label="Email"
                type="email"
                v-model="user.email"
                :rules="this.emailRules"
                required
              ></v-text-field>
              <v-text-field
                label="Asal sekolah"
                type="text"
                v-model="user.origin"
                :rules="this.originRules"
                required
              ></v-text-field>
              <div class="text-center">
                <v-btn
                  type="submit"
                  class="mb-8 success"
                  width="100%"
                  :disabled="!isFormValid || isLoading"
                  :loading="isLoading"
                  @click.prevent="register()"
                >Register</v-btn>
                <router-link :to="{name: 'Login'}">Kembali ke login</router-link>
              </div>
            </v-form>
            <div class="h6 red--text" :hidden="errorMessage.length === 0">{{ errorMessage }}</div>
          </v-card>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import User from "../models/userModel";
import inputFormMixin from "../mixins/inputFormMixin";
import authMixin from "../mixins/authMixin";
import errorMixin from "../mixins/errorMixin";

export default {
  name: "Register",
  data: () => ({
    user: new User(),
    isFormValid: false,
    isLoading: false,
    errorMessage: "",
  }),
  computed: {
    isLoggedIn() {
      return this.$store.state.auth.status.isLoggedIn;
    },
  },
  methods: {
    register: async function () {
      this.isLoading = true;
      try {
        let registerResponse = await this.$store.dispatch(
          "auth/register",
          this.user
        );
        if (registerResponse.status === 200) {
          this.redirectLogin(
            "Anda berhasil mendaftar, mohon cek WhatsApp anda."
          );
        }
      } catch (error) {
        this.errorMessage = this.getErrorMessage(error);
      }
      this.isLoading = false;
    },
  },
  created() {
    if (this.isLoggedIn) {
      this.$router.push("home");
    }
  },
  mixins: [inputFormMixin, authMixin, errorMixin],
};
</script>